
.articlesList {
    margin-top: 50px;
    margin-bottom: 50px;
}
li {
    list-style-type:none;
    width:100%;
    word-break:break-all;
}
.chose_page_button {
    border-top: 1px solid rgb(150, 150, 150);
    border-bottom: 1px solid rgb(150, 150, 150); 
    text-align: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -ms-flex-line-pack: center;
        align-content: center;
    /*border-radius: 15px;*/
    width: 110px;
    height: 30px;
    float:left;
    padding: 0;
    margin: 0;
    cursor: pointer;
}
.switch_page {
    padding-top: 30px;
    height: 30px;
    color: rgb(150, 150, 150);
    font-size: 12px;
}
.switch_page p {
    padding:0;
    margin:0;
    padding-top:7px;
}



