/*@import './component/css/easydialog.min.css';*/
.home[data-v-b4d5a158] {
    position: absolute;
    width: 100%;
    height: 100%;
    overflow-y: hidden;
}
.home_navigation[data-v-b4d5a158] {
    width: 250px;
    height: 100%;
    background-color: rgb(55, 55, 55);
    /*background: rgb(55, 55, 55) url(../assets/background_cloud.gif) no-repeat center;
    background-size: 1150px 800px;*/
    float: left;
    position: relative;
    overflow: hidden;
}
.home_navigation_phone[data-v-b4d5a158] {
    width: 100%;
    position: fixed;
    height: 50px;
    background-color: rgb(55, 55, 55);
}
.blur[data-v-b4d5a158] {	
	filter:blur(10px);
	-webkit-filter:blur(10px);
	-moz-filter:blur(10px);
	-ms-filter:blur(10px);
	-o-filter:blur(10px);
}
/*头像*/
.avatar_border[data-v-b4d5a158] {
	position: relative;
	width:66px;
	height:66px;
	border-radius:33px;
	border:1px solid white;
	background-color:white;
	left: 50%;
	margin-left: -33px;
}
.avatar_view[data-v-b4d5a158] {
	width:60px;
	height:60px;
	border-radius:30px;
	border:1px solid white;
	overflow:hidden;
	margin-left:2px;
	margin-top:2px;
}
.avatar_view img[data-v-b4d5a158] {
	width:100%;
	height:100%;
}
.my_info_view[data-v-b4d5a158] {
    text-align:center;
    -webkit-box-align:center;
        -ms-flex-align:center;
            align-items:center;
	width:150px;
	position: relative;
	left: 50%;
	margin-left: -75px;
	overflow: hidden;
	margin-top: 20px;
}
.my_info_view ul[data-v-b4d5a158] {
    height: 100%;
}
.my_info_view li[data-v-b4d5a158] {
	float: left;
	width: 33%;
	height: 20px;
	overflow: hidden;
	list-style-type:none;
}
.my_info_view li img[data-v-b4d5a158] {
	width:20px;
	height:20px;
}


/*目录*/
.catalog_div[data-v-b4d5a158] {
    width: 100%;
    color: white;
    text-align: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    overflow: hidden;
}
.catalog_div li[data-v-b4d5a158] {
    position: relative;
    width: 150px;
    left: 50%;
    margin-left: -75px;
	height: 30px;
    overflow: hidden;
	list-style-type:none;
    border-bottom: 1px solid white;
}
.catalog_div p[data-v-b4d5a158] {
    padding: 0;
    padding-top: 7px;
    margin: 0;
    cursor: pointer;
}

/*路由页面*/
.router_view[data-v-b4d5a158] {
    height: 100%;
    overflow: hidden;
}

/*底部说明*/
.bottom_illustrate_view[data-v-b4d5a158] {
    font-size:12px;
    color:rgb(150, 150, 150);
    padding-bottom: 20px;
}
.bottom_illustrate_view p[data-v-b4d5a158] {
    line-height: 20px;
}
.bottom_illustrate_view a[data-v-b4d5a158]:link {
    color:rgb(55, 55, 55);
}
.bottom_illustrate_view a[data-v-b4d5a158]:visited {
    color:rgb(150, 150, 150);
}
.bottom_illustrate_view a[data-v-b4d5a158]:hover {
    color:rgb(55, 55, 55);
}
.bottom_illustrate_view a[data-v-b4d5a158]:active {
    color:rgb(55, 55, 55);
}



